
import numberComma from 'vux/src/tools/number/comma.js'

import {toFixed} from 'plugins/calc'

export default {
name: "MatterItem",
props: {
item: {
type: Object,
default() {
return {}
}
},

isReturnMatter: {
type: Boolean,
default: false
}
},
data() {
return {
amtList: ['', '', '', '', '', '', '', '', '', '', ''],
}
},
filters: {
numberComma,
toFixed,
},
methods: {

getMatterDefault(item) {
let url = require('@/assets/wl_default.png');
if (item) {
item.inventoryPic = url;
}
return url
},
},
created() {
}
}



