// mixin
import saleCommon from 'mixins/saleCommon'
// 
import {toFixed, accAdd, accMul, accDiv} from 'plugins/calc'

const BASIC_INFO_KEY = 'HELP_BASIC_INFO';
const FORM_INFO_KEY = 'HELP_FORM_INFO';
export default {
  name: 'help-Report',
  mixins: [saleCommon],
  data() {
    return {
      areaList: [],
      bankList: [],
      areaValue: [],
      bankValue: [],
      areaToday: [],
      cityToday: [],
      countyToday: [],
      bankToday: [],
      areaTomorrow: [],
      bankTomorrow: [],
      areaNextWeek: [],
      bankNextWeek: [],
      areaNextWeekList: [],
      bankNextWeekList: [],
      provinceList: [],
      cityTodayList: [],
      countyTodayList: [],
    };
  },
  computed: {
    AclassTotal() {
      let total = 0;
      this.arr.forEach(item => {
        if (item.qty) {
          total = accAdd(total, accMul(item.qty, item.amount));
        }
      });
      total = accAdd(total, this.Aclass);
      return total;
    },
    otherAclass() {
      // return accDiv(this.Aclass, 1996);
      return toFixed(accDiv(this.Aclass, 1996))
    },
    BclassTotal() {
      return accAdd(this.Bclass, this.BclassDown)
    },
    // B
    BSet() {
      // return toFixed(this.Bclass / 4000)
    },
    // B
    BSetDown() {
      // return toFixed(this.BclassDown / 5000)
    },
    // B
    BSetTotal() {
      // return toFixed(accAdd(this.BSet, this.BSetDown))
    },
  },
  methods: {
    //
    goCount() {
      if (!this.btnStatus) return;
      const ROSE_OPTION = JSON.parse(localStorage.getItem("ROSE_OPTION"));  //
      //
      let governor = this.governor,
        member = this.member,
        comments = this.comments;
      //
      let date = new Date(),
        year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate();

      // 
      let tips = '';
      let tipArr = [
        {key: 'bankValue', msg: ''},
        {key: 'areaValue', msg: ''},
        {key: 'arr', msg: ''},
        {key: 'Aclass', msg: 'A'},
        {key: 'Bclass', msg: 'B()'},
        {key: 'BclassDown', msg: 'B()'},
        {key: 'hotelAmt', msg: ''},
        {key: 'trafficAmt', msg: ''},
        {key: 'lTrafficAmt', msg: ''},
        {key: 'otherAmt', msg: ''},
      ];
      // this.baseinfoExt.varchar14 = this.areaToday[0];
      // this.baseinfoExt.varchar15 = this.bankToday[0];
      // this.baseinfoExt.varchar19 = this.areaTomorrow[0];
      // this.baseinfoExt.varchar20 = this.areaNextWeek[0];
      // this.baseinfoExt.varchar21 = this.bankNextWeek[0];
      // this.baseinfoExt.varchar22 = this.bankTomorrow[0];
      // this.baseinfoExt.varchar23 = this.cityToday[0];
      // this.baseinfoExt.varchar24 = this.countyToday[0];
      // let validateMap = [
      //   {key: 'varchar14', msg: ''},
      //   {key: 'varchar23', msg: ''},
      //   {key: 'varchar24', msg: ''},
      //   {key: 'varchar15', msg: ''},
      //   {key: 'varchar17', msg: ''},
      //   {key: 'varchar18', msg: ''},
      //   {key: 'varchar19', msg: ''},
      //   {key: 'varchar22', msg: ''},
      //   {key: 'varchar20', msg: ''},
      //   {key: 'varchar21', msg: ''},
      // ];
      tipArr.every(item => {
        if (!this[item.key] && this[item.key] !== 0) {
          tips = `${item.msg}`;
          return;
        }
        else if (item.key === 'areaValue' && !this[item.key].length || this[item.key][0] === '' || item.key === 'bankValue' && !this[item.key].length) {
          tips = `${item.msg}`;
          return;
        }
        else if (item.key === 'arr') {
          if (!this.arr.length) {
            tips = `${item.msg}`;
            return false;
          }
          this[item.key].every(item => {
            if (item.value !== '' && !item.qty && item.qty !== 0) {
              tips = '';
              return false
            }
            return false
          });
          return !tips;
        }
        return true;
      });
      // !tips && validateMap.every(item => {
      //   if (!this.baseinfoExt[item.key]) {
      //     tips = item.msg;
      //     return false
      //   }
      //   return true
      // });
      if (tips) {
        this.$vux.alert.show({
          content: tips
        });
        return;
      }
      // 
      let jsonData = {
        listId: "4bda3e47-a088-4749-a988-ebb07cfb00e4",
        referenceId: this.guid(),
        baseinfoExt: {
          id: this.guid(),
          varchar1: ROSE_OPTION.dept,         //  () ()
          // varchar2: this.monthCoverNum,       // 
          varchar3: this.areaValue[0],        // ()
          varchar4: this.bankValue[0],        // 
          varchar5: ROSE_OPTION.groupName,    //  ()
          varchar6: '',                     // 
          varchar7: governor,                 // 
          varchar8: member,                   // 
          varchar9: comments,                 // 
          varchar10: ROSE_OPTION.region,      // ()
          varchar11: ROSE_OPTION.bank,        // ()
          varchar12: ROSE_OPTION.userCode,    // 
          varchar13: this.costVolumeRatio,    // 
          double7: this.hotelAmt, // 
          double8: this.otherAmt, // 
          double9: this.totalCost, // (+)
          double10: this.trafficAmt, // 
          double11: this.lTrafficAmt, // 
          integer1: year,     //
          integer2: month,     //
          integer3: day,        //
          ...this.baseinfoExt,
          // double12: this.AclassTotal, // A
          // double13: this.BclassTotal, // B
        },
        transDetailUncalc: [{
          id: this.guid(),
          transObjCode: "A", //
          containerCode: "A", //
          qty: "",
          amount: Number(this.Aclass), //
          fgCode: "",
          num1: this.otherAclass
        }, {
          id: this.guid(),
          transObjCode: "A", //
          containerCode: "A", //
          qty: "",
          amount: Number(this.AclassTotal), //
          fgCode: ""
        }, {
          id: this.guid(),
          transObjCode: "B()", //
          containerCode: "B", //
          qty: "",
          amount: Number(this.Bclass), //
          fgCode: "",
          num1: this.BSet,
        }, {
          id: this.guid(),
          transObjCode: "B()", //
          containerCode: "B", //
          qty: "",
          amount: Number(this.BclassDown), //
          fgCode: "",
          num1: this.BSetDown,
        }, {
          id: this.guid(),
          transObjCode: "B", //
          containerCode: "B", //
          qty: "",
          amount: Number(this.BclassTotal), //
          fgCode: "",
        },
        ],
        transCode: "XHXSDD"
      };
      let newVar2 = jsonData.baseinfoExt.varchar2;
      newVar2 = accAdd(newVar2, this.BSet);
      newVar2 = accAdd(newVar2, this.BSetDown);
      // 
      for (let item of this.arr) {
        //  
        // newVar2 = toFixed(accAdd(newVar2, Number(item.num1)));
        // 
        jsonData.transDetailUncalc.push({
          id: this.guid(),
          transObjCode: item.value, //
          containerCode: "", //
          qty: item.qty,
          taxAmount: item.taxAmount,
          amount: item.qty * item.amount, //
          fgCode: "",
          num1: item.num1, // 
        });
      }
      jsonData.baseinfoExt.varchar2 = newVar2;
      this.totalInfo = {
        isMobile: true,
        conn: 20000,
        list: "trans_form_data",
        transCode: "XHXSDD",
        jsonData: JSON.stringify(jsonData)
      };
      //  
      localStorage.setItem(
        "saleReportInfo",
        JSON.stringify({
          saleReportRemark: this.totalInfo,
          time: new Date().getTime()
        })
      );
      //  
      this.recordForm();
      //  
      this.recordBasic();
      this.$router.push({path: "/count"});
    },
    // 
    recordBasic() {
      //  
      localStorage.setItem(
        [BASIC_INFO_KEY],
        JSON.stringify({
          bank: this.bankValue[0],
          areaValue: this.areaValue[0],
        })
      );
    },
    // 
    recordForm() {
      localStorage.setItem(
        [FORM_INFO_KEY],
        JSON.stringify({
          Aclass: this.Aclass,
          Bclass: this.Bclass,
          BclassDown: this.BclassDown,
          saleReportArr: this.arr,
          comments: this.comments,
          hotelAmt: this.hotelAmt,
          trafficAmt: this.trafficAmt,
          lTrafficAmt: this.lTrafficAmt,
          otherAmt: this.otherAmt,
          time: new Date().getTime(),
          baseinfoExt: this.baseinfoExt,
        })
      );
    }
  },
  created() {
    let {monthCoverNum = ''} = this.$route.query;
    this.baseinfoExt.varchar2 = Number(monthCoverNum);
    // 
    const basicInfo = JSON.parse(localStorage.getItem(BASIC_INFO_KEY)) || '';
    const formInfo = JSON.parse(localStorage.getItem(FORM_INFO_KEY)) || '';
    // 
    this.echoStorage(basicInfo, formInfo);
    // 
    this.getSuperior();
    // 
    this.getArea();
    // 
    this.getBank();
    (async () => {
      await this.getProvince();
      if (formInfo) {
        await this.getCity();
        this.getCounty();
      }
    })()
  },
  beforeRouteLeave(to, from, next) {
    if (!this.arr.length || to.name == "Count") next();
    else {
      this.$vux.confirm.show({
        title: "",
        content: "",
        confirmText: "",
        cancelText: "",
        onCancel: () => {
          localStorage.removeItem(BASIC_INFO_KEY);
          localStorage.removeItem(FORM_INFO_KEY);
          next();
        },
        onConfirm: () => {
          this.recordForm();
          //
          this.recordBasic();
          next();
        }
      });
    }
  }
};



