
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import myReportService from "service/myReportService";
import {setTimeout} from "timers";
import loading from "components/common/loading";

import {toFixed, accAdd, accMul} from 'plugins/calc'

const list = () => ["", "", "", ""];
export default {
components: {
Tab,
TabItem,
Divider,
loading,
SwiperItem,
FormPreview,
CellFormPreview
},
data() {
return {
listTab: list(),
demo2: "",
list1: [],
list2: [],
list1Total: 0,
AclassTotal: 0,
BClassTotal: 0,
remark: "",
spinner: false,
totalCoverNum: 0, // 
costSalesRatio: '', // 
bClassList: [], // bitem
};
},
filters: {
numberComma
},
methods: {
mylist(tab) {
this.totalCoverNum = 0;
this.costSalesRatio = '';
if (tab == 0) {
this.listpanl(this.remark.days);
} else if (tab == 1) {
this.listpanl(this.remark.weeks);
} else if (tab == 2) {
this.listpanl(this.remark.months);
} else if (tab == 3) {
this.listpanl(this.remark.years);
}
},
listData() {
let ROSE_OPTION = JSON.parse(localStorage.getItem("ROSE_OPTION"));
let jsonData = {




shengName: '',
bankName: '',
sybName: '',
bmName: '',
};
myReportService
.myRepotList(jsonData)
.then(data => {
if (data) {
this.spinner = false;
}
this.remark = data;
this.mylist(0);
})
.catch(data => {
this.spinner = true;
});
},
listpanl(m) {
let AclassTotal = 0;
let BClassTotal = 0;
let list2 = [];
let bClassList = [];
let hotelAndElseCost = 0;
let totalCoverNum = 0;
this.list1Total = 0;
this.list1.length = 0;
this.list2.length = 0;

for (let i = 0; i < m.length; i++) {
let item = m[i];
switch (item.objType) {
case '':
if (item.objName) {
this.list1Total += Number(item.amount);
this.list1.push({
label: item.objName,
value: `${numberComma(toFixed(item.amount))} (${item.qty}/${toFixed(item.coverNum || 0)})`,
});
totalCoverNum = accAdd(totalCoverNum, item.coverNum);
}
break;
case 'A':
if (item.objName === 'A') {
AclassTotal = accAdd(AclassTotal, item.amount);
list2 = [
...list2,
{
label: '()',
value: '',
}, {
label: '',
value: item.hotelCost ? `${numberComma(toFixed(Number(item.hotelCost)))}` : '',
}, {
label: '',
value: item.cityTrafficCost ? `${numberComma(toFixed(Number(item.cityTrafficCost)))}` : '',
}, {
label: '',
value: item.longCityTrafficCost ? `${numberComma(toFixed(Number(item.longCityTrafficCost)))}` : '',
}, {
label: '',
value: item.elseCost ? `${numberComma(toFixed(Number(item.elseCost)))}` : '',
}, {
label: '',
value: item.hotelAndElseCost ? `${numberComma(toFixed(Number(item.hotelAndElseCost)))}` : '',
},
];
hotelAndElseCost = item.hotelAndElseCost;
} else {
list2.unshift({
label: item.objName,
value: `${toFixed(item.amount)}`,
});
}
break;
case 'A':
list2 = [
{
label: 'A',
value: `${numberComma(toFixed(item.amount))}/${Number(item.coverNum)}`,
},
...list2
]
totalCoverNum = accAdd(totalCoverNum, item.coverNum);
break;
case 'B':
if (item.objName === 'B') {
BClassTotal = accAdd(BClassTotal, item.amount);
} else {
totalCoverNum = accAdd(totalCoverNum, item.coverNum);
bClassList.push({
label: item.objName,
value: `${numberComma(toFixed(item.amount))}/${Number(item.coverNum)}`,
});
}
break;
default:
break;
}
}
this.totalCoverNum = totalCoverNum;
this.list2 = list2;
this.bClassList = bClassList;
this.costSalesRatio = Number(AclassTotal) !== 0 ? `${toFixed(accMul(hotelAndElseCost / AclassTotal, 100))}%` : '';
this.AclassTotal = AclassTotal;
this.BClassTotal = toFixed(BClassTotal);
this.list1Total = toFixed(this.list1Total);
}
},
created() {
this.spinner = true;
this.listData();
}
};



