import $axios from '../plugins/ajax'

//   
export let getProductCount = () => {
  return $axios.ajax({
    url: '/ds/getProjectProductCoverNum',
    data: {
      noCount: 1
    }
  })
}
//  
export let getCurrMonthStatus = () => {
  return $axios.ajax({
    url: '/mobile/getCurrMonthStatusByQuarter'
  })
}
export default {
  getProductCount,
  getCurrMonthStatus
}



