
import dateFormat from 'vux/src/tools/date/format.js'


export default {
name: "RTimer",
props: {
widthScale:{
type : Number,
default : 300
}
},
data() {
return {
toDay: '',
preDate: '',
defaultTips: '',
timeFilter: {
startDate: '',             // 
EndDate: '',               // 
},
dateShow: false,                  // 
}
},
methods: {

getStart() {
this.$vux.datetime.show({
cancelText: '',
confirmText: '',
value: this.timeFilter.startDate,
endDate: this.preDate,
onConfirm: (val) => {
this.timeFilter.startDate = val;
},
})
},

getEnd() {
this.$vux.datetime.show({
cancelText: '',
confirmText: '',
value: this.timeFilter.EndDate,
endDate: this.toDay,
onConfirm: (val) => {
this.timeFilter.EndDate = val;
},
})
},

resetData() {
this.timeFilter = {startDate: '', EndDate: '',};
},

confirm() {

if (this.timeFilter.startDate !== '' && this.timeFilter.EndDate !== '') {
this.defaultTips = `${this.timeFilter.startDate}~${this.timeFilter.EndDate}`;
}

else if (this.timeFilter.EndDate !== '') {
this.defaultTips = ` ${this.timeFilter.EndDate}`;
}

else if (this.timeFilter.startDate !== '') {
this.defaultTips = `${this.timeFilter.startDate}~${this.toDay} []`;
}

else {

this.defaultTips = ''
}

this.dateShow = false;

let timeFilter = {
startDate: this.timeFilter.startDate === '' ? '' : this.timeFilter.startDate,
endDate: this.timeFilter.EndDate === '' ? '' : this.timeFilter.EndDate
};
this.$emit('on-select', timeFilter);
},
},
created() {
this.toDay = dateFormat(new Date(), 'YYYY-MM-DD');
this.preDate = dateFormat(new Date(new Date().getTime() - 24 * 60 * 60 * 1000), 'YYYY-MM-DD');

this.defaultTips = '';
}
}



